

.card {
  background: #fff;
  border-radius: 2px;
  height: 225px;
  margin: 1rem;
  position: relative;
  width: 225px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  overflow: hidden;
  text-align: center;
  width: 100%
}

.wrapper {
  
  display: flex;
  min-height: 80%;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 150px;
  padding-bottom: 50px;
  width: 90%;
  margin: 0 auto;
}

header {
  background: darkgrey;
  min-height: 70px;
  position: fixed;
  padding: 5px;
  width: 100%;
  color: #000;
  text-align: center;
  box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
  z-index: 99;
  white-space: nowrap;
  
}

